@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p,
table,
th,
td,
form,
fieldset,
legend,
textarea,
input,
select,
textarea,
button,
article,
aside,
dialog,
footer,
header,
section,
footer,
nav,
figure,
main {
    margin: 0;
    padding: 0
}

html,
body {
    -webkit-text-size-adjust: none;
    -ms-text-size-adjust: none;
} 

body,
input,
select,
textarea,
button {
    font-family: $globalFont;
}

ul,
ol {
    list-style: none;
}

input,
button,
select,
textarea {
    -webkit-appearance: none;
    appearance: none;
    border: 0;
    border-radius: 0;
    background-color: transparent
}

img,
fieldset {
    border: 0;
    vertical-align: top;
}

em,
address {
    font-style: normal;
}

.blind,
caption span,
legend {
    overflow: hidden;
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    clip: rect(0,0,0,0);
}